<script>
    import { _ } from 'svelte-i18n'
	import { locale, locales } from 'svelte-i18n'
    import WeekGraph from '../components/WeekGraph.svelte'
	import WeekGraphSeconds from '../components/WeekGraphSeconds.svelte'
	import {endChallenge} from '../utils/bridge.js'
	import {store_weeknr} from '../stores.js'
	import { onMount } from 'svelte';

	onMount( 
         () => {
			// progressbar
			const fullbar = 235.75
			const oneWeek = fullbar / 8
			const wkNr = parseInt($store_weeknr)
			const bar = wkNr * oneWeek 
			document.getElementById("progress").setAttribute('width',bar)


			// changing medals
            const imageMedal = document.getElementById("medal")
			imageMedal.src  = "./medal2.svg"
            if (wkNr > 3)
			 imageMedal.src = "./silver medal.svg"
			if (wkNr > 7)
			{
			 imageMedal.src = "./gold medal.svg"

			
			 // confetti
			const Confettiful = function (el) {
    		this.el = el;
    		this.containerEl = null;
  
            this.confettiFrequency = 3;
			this.confettiColors = ["#fce18a", "#ff726d", "#b48def", "#f4306d"];
			this.confettiAnimations = ["slow", "medium", "fast"];
  
    		this._setupElements();
    		this._renderConfetti();
  			};
  
  			Confettiful.prototype._setupElements = function () {
    			const containerEl = document.createElement("div");
    			const elPosition = this.el.style.position;
  
    			if (elPosition !== "relative" || elPosition !== "absolute") {
      			this.el.style.position = "relative";
    			}
  
    			containerEl.classList.add("confetti-container");
  
    			this.el.appendChild(containerEl);
  
    			this.containerEl = containerEl;
  			};
  
  			Confettiful.prototype._renderConfetti = function () {
    			this.confettiInterval = setInterval(() => {
      			const confettiEl = document.createElement("div");
      			const confettiSize = Math.floor(Math.random() * 3) + 7 + "px";
      			const confettiBackground = this.confettiColors[
       				 Math.floor(Math.random() * this.confettiColors.length)
      			];
      			const confettiLeft = Math.floor(Math.random() * this.el.offsetWidth) + "px";
      			const confettiAnimation = this.confettiAnimations[
        		Math.floor(Math.random() * this.confettiAnimations.length)
      			];
  
      			confettiEl.classList.add(
        		"confetti",
        		"confetti--animation-" + confettiAnimation
      			);
      			confettiEl.style.left = confettiLeft;
      			confettiEl.style.width = confettiSize;
      			confettiEl.style.height = confettiSize;
      			confettiEl.style.backgroundColor = confettiBackground;
  
      			confettiEl.removeTimeout = setTimeout(function () {
        		confettiEl.parentNode.removeChild(confettiEl);
      			},3000);
  
      			this.containerEl.appendChild(confettiEl);
    		}, 5);
  		};
  
  		window.confettiful = new Confettiful(document.querySelector(".js-container"));
        
	  }
    });



	function goBack() {
		console.log("back button")
		endChallenge()
	}

	var weekJumps = []
	var weekSeconds = []

	var i = 0;
	for ( i=1; i < 9; i++)
	{
		const txtJumpsThisWeek = "Jumpsweek" + i.toString() // weeknr
		if (window.localStorage.getItem(txtJumpsThisWeek) != undefined)
		{  
			let val = window.localStorage.getItem(txtJumpsThisWeek); 
			console.log("push :" + val)
			weekJumps.push(val)
		}
    	const txtSecondsThisWeek = "Secondsweek" + i.toString()
		if (window.localStorage.getItem(txtSecondsThisWeek) != undefined)
		{  
			let val = window.localStorage.getItem(txtSecondsThisWeek); 
			console.log("push :" + val)
			weekSeconds.push(val)
		}
	}
	console.log("weeknr in endscfreen :")
	console.log($store_weeknr)

</script>

<style>


:global(body)
 {
  margin: 0;
  padding: 0;
  background-color: #263238;
}
@keyframes confetti-slow {
  0% {
    transform: translate3d(0, 0, 0) rotateX(0) rotateY(0);
  }
  100% {
    transform: translate3d(25px, 105vh, 0) rotateX(360deg) rotateY(180deg);
  }
}
@keyframes confetti-medium {
  0% {
    transform: translate3d(0, 0, 0) rotateX(0) rotateY(0);
  }
  100% {
    transform: translate3d(100px, 105vh, 0) rotateX(100deg) rotateY(360deg);
  }
}
@keyframes confetti-fast {
  0% {
    transform: translate3d(0, 0, 0) rotateX(0) rotateY(0);
  }
  100% {
    transform: translate3d(-50px, 105vh, 0) rotateX(10deg) rotateY(250deg);
  }
}
:global(.confetti-container) {
  perspective: 700px;
  position: absolute;
  overflow: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
:global(.confetti) {
  position: absolute;
  z-index: 1;
  top: -10px;
  border-radius: 0;
}
:global(.confetti--animation-slow) {
  animation: confetti-slow 3s linear 1 forwards;
}
:global(.confetti--animation-medium) {
  animation: confetti-medium 5s linear 5 forwards;
}
:global(.confetti--animation-fast) {
  animation: confetti-fast 5s linear 1 forwards;
}




