	<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 268.67 23.03">
		<defs><style>.cls-1{fill:#f4f4f4;}.cls-1,.cls-2{stroke:#9e9e9e;stroke-miterlimit:10;stroke-width:0.3px;}
					 .cls-2{fill:#9e8c99;}.cls-3{font-size:12px;fill:#1d1d1b;font-family:FuturaPT-Medium, Futura PT;font-weight:500;}
		</style></defs>
		<g id="Layer_2" data-name="Layer 2">
		<g id="Layer_1-2" data-name="Layer 1">
		<rect class="cls-1" x="16.85" y="18.37" width="235.75" height="4.51" rx="2.25"/>
		<rect id="progress" class="cls-2" x="16.85" y="18.37" width="117.87" height="4.51" rx="2.25"/>
		<text class="cls-3" transform="translate(14.02 10)">W1</text>
		<text class="cls-3" transform="translate(236.55 10)">W8</text>
