<script lang="ts">
import { timeFormat, eTrainingStatus } from "../utils/utils";



export let timeTotal = 120; 
export let status:eTrainingStatus = eTrainingStatus.IDLE;


var timerPlay = null;
var nrSeconds = 0;
var maxTime =  0;
$: barWidth = ( timeTotal/maxTime)*100;

$: timeTxt = timeFormat(timeTotal);

function oneSec() {
   if (timeTotal < maxTime) 
      timeTotal++;
  else 
  {
     clearInterval(timerPlay);
     timerPlay = null;
     timeTotal = maxTime;
     maxTime = 0;
  }
}

function setStatus(){
  
  switch(status) {
    case eTrainingStatus.IDLE:
       status = eTrainingStatus.PLAY;
       maxTime = timeTotal;
       timeTotal = 0;
       timerPlay =  setInterval(oneSec, 1000);
    break;
    case eTrainingStatus.PLAY:
       status = eTrainingStatus.PAUSED;
       clearInterval(timerPlay); // stop the counter
       timerPlay = null;
    break;
    case eTrainingStatus.PAUSED:
       status = eTrainingStatus.PLAY;
       timerPlay =  setInterval(oneSec, 1000); // restart timer
    break;

  default:
     status = eTrainingStatus.IDLE;
     if (timerPlay !=null) 
        clearInterval(timerPlay);
     nrSeconds = 0;
  }
}
</script>



<div class="container border" style="width: 100%; height:100%;  ">
    <div class="RoundingBox" >
      <div  on:click="{setStatus}" class="PlayButton center" style="width: 100%; height:100%; ">
        {#if ((status == eTrainingStatus.PAUSED) ||  (status == eTrainingStatus.IDLE)) }
          <img  style=" height:100%; width:100%  "
                src="https://lifesensegroup.github.io/audio/VectorPlayButton.svg"
                alt="playButton" />
        {:else}
          <img  style=" height:100%; width:100%  "
                src="https://lifesensegroup.github.io/audio/pauseButton.svg"
                alt="pauseButton" />
        {/if}
      </div>
         
      <div id="time" class="Time txt" style = "color: white ">
        {timeTxt}
      </div>
       
      <div class="TrainingTxt txt border" style = "color: white">
        contract and relax
      </div>
        

      <div class="ProgressBar border centerLeft rounded" style="width: 98%; height:50%;   background: grey;">
        <div id="Bar" class="rounded" style="width: {barWidth}%"  ></div>
      </div>
    </div>
      
</div>

