
<script context="module">  // this is only run once for this 'class/module' so like a static method
	let current;
</script>

<script>
  import { createEventDispatcher } from 'svelte';
  import { VimeoPlayer } from 'svelte-vimeo-player';
import { customElement } from 'lit-element';

  


  const dispatch = createEventDispatcher();
  
	export let src = "";
	export let title;
  export let code;
  export let description;
  export let thumbnail;

	

	let video;
	let paused = true;
  let player

	function stopOthers() {
    dispatch("child-click-video", {code}) // send an event with the code to the caller
    if (current && current !== video) 
       current.pause();
		current = video;
	}


</script>

<article class:playing={!paused}>

    <div class="container" >
        <div class="item1 grid-item-centered-contents">
            <!-- svelte-ignore a11y-media-has-caption -->


  

            <vm-player autoplay muted>
              <vm-video poster={thumbnail} cross-origin>
                <!-- Why `data-src`? Lazy loading. You can always use `src` if you don't need it. -->
                <source data-src={src} type="video/mp4" />
                
              </vm-video>
            
              <!-- Loads the default Vime UI. -->
              <vm-default-ui />
            </vm-player>

            <VimeoPlayer
  videoId={src}
  bind:player={player}

/>



        </div>
        
        <div class="item2 grid-item-centered-contents">
            <span>  {title} </span>
        </div>

        <div class="item3 grid-item-centered-contents scroll">

            {#if (typeof(description) != "undefined") }
               <span> {description}   </span>
            {:else}
            <span>     </span>
            {/if}

        </div>
    </div>
</article>



<style>

.scroll {
            display: block;
            width: 100%;
            height: 100px;
            overflow-y: scroll;
        }

    /* too make text centered in grim-items, https://stackoverflow.com/questions/45536537/centering-in-css-grid */
.grid-item-centered-contents {
  position: relative;
  text-align: center;
}

span {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
/* too make text centered in grim-items end */

	article {
		margin: 0 0 1em 0; 
    border-style: dotted dashed;
  border-width: 2px;
  border-color: red; 
	}
	 p {
		margin: 0 0 0.3em 0;
	}
	video {
		width: 100%; margin: 0.5em 0 1em 0;
	}
	.playing {
		color: #ff3e00;
	}

    div {
        vertical-align: center;
        border-style: dotted dashed;
  border-width: 2px;
  border-color: red; 
      }
      
      .container {
        display: flex;
      }
      
      .item1 {
        width: 50%;
        flex-grow: 1;
      }
    
      .item2 {
        width: 30%;
        flex-grow: 1;
      }

      .item3 {
        width: 20%;
        flex-grow: 1;
      }
    

</style>