export async function storeUserInfo(user) {
    const db2 = await openDBlocal('carin', 3);
    // loop over all items in user JSON and store them in the DB
    // const keys = Object.keys(user);
    const values = Object.values(user);
    for (var i = 0; i < values.length; i++) {
        var value, key;
        try {
            value = values[i].value.value;
            key = values[i].value.id;
            if (key == undefined) {
                value = values[i].value;
                key = values[i].id;
            }
        }
        catch (error) {
            if (values[i].value != undefined) {
                value = values[i].value;
                key = values[i].id;
            }
            else {
                value = values[i];
                key = Object.keys(user)[i];
            }
        }
        await db2.put('user', { id: key, value: value });
        if (key == "name") {
            store_customer_name.set(value);
        }
    }
    db2.close();
}
export async function storeGetSettingInfo() {
    const db = await openDBlocal('carin', 3);
    const settings = await db.getAll('settings');
    if (settings.length > 0) {
        // @ts-ignore
        //await storeUserInfo(events)
        var len = settings.length;
        // ignore typescript error
        for (var i = 0; i < len; i++) {
            var infoElement = settings[i];
            // @ts-ignore
            if (typeof infoElement.id != 'undefined') {
                if (infoElement.id == 'weeknr') {
                    // @ts-ignore
                    store_weeknr.set(infoElement.value);
                }
            }
        }
        console.log(store_weeknrValue);
    }
    db.close();
}
