import { store_nativeSystemType, store_weeknr } from '../stores.js';

let store_nativeSystemType_value;
let store_weeknr_value;

var webSocket;

store_nativeSystemType.subscribe(value => {
		store_nativeSystemType_value = value;
	});

  store_weeknr.subscribe(value =>{
    store_weeknr_value = value;
  })


export function initNativeBridge() {


  if (store_nativeSystemType_value == "android") {
    webSocket = new WebSocket("ws://localhost:8080");
    webSocket.onopen = function (event) {

    };

    webSocket.onmessage = function (event) {
      console.log("event RECEIVED")
      console.log(event.data);
      var obj = JSON.parse(event.data);
      console.log(obj)
      if (typeof obj.progress != 'undefined') {
        var txt = obj.progress;
        // fill in progressbar
        var integer = parseInt(txt, 10);
        elem.value = integer;
        elem2.innerText = txt + '%'
      }
      if (typeof obj.activateButton != 'undefined') {
        console.log("inside activeButton")
        var txt = obj.activateButton;
        // enable or disable the challenge button
        if (txt == 'true')
        {
          elemStart.disabled = false;
          elemStart.style.background='#15B4B0';
          elemStart.style.bordercolor='#15B4B0';
        }
        else
        {
          elemStart.disabled = true;
          elemStart.style.background='grey';
          elemStart.style.borderColor='white';
        }
      }
      if (typeof obj.weeknrTxt != 'undefinded')
      {
        var txt = obj.weeknrTxt

        if ((txt == 1) ||  (txt == 2) ||  (txt == 3) ||  (txt == 4) ||  (txt == 5) ||  (txt == 6) ||  (txt == 7) ||  (txt == 8)) 
        {
        console.log("before update")
        
         
         //txt = '2'
         console.log(txt)
         store_weeknr.set(txt)
         console.log("na update")
        }

      }
   }
  }

} 

export function endChallenge()
{
   console.log("end challange")

  if (store_nativeSystemType_value == "webapp")
	{
    window.location.href='#/';
  }

	if (store_nativeSystemType_value == "ios")
	{
    console.log("end challange ios")
		window.webkit.messageHandlers.test.postMessage({name:"test", cmd:"challenged finished"});
	}
	else
	{
    console.log("end challange android")
		sendText()
	}
}

