export function setWeekNr(txt)
{
  console.log(txt)
  store_weeknr.set(txt)
  console.log("na update weeknr")
}

export function setProgress(txt)
{
    var elemx = document.getElementById("progressbar");
    var strs = txt.split('%')
    var num = parseInt(strs[0])
    elemx.value = num;
    var elemy = document.getElementById("percTxt")
    elemy.innerText =  txt
}

export function enableChallengeButton()
{
    var elemx = document.getElementById("start");
    elemx.disabled = false;
    elemx.style.background='#15B4B0'
    elemx.style.bordercolor='#15B4B0'
}

export function disableChallengeButton()
{
    var elemx = document.getElementById("start");
    elemx.disabled = true;
    elemStart.style.background='grey';
}


/* ------------------   android only ------------------ */

export function goBack() {
    sendText()
  }
  
   // Send text to all users through the server
 export function sendText() {
    // Construct a msg object containing the data the server needs to process the message from the chat client.
    var msg = {
      type: "action",
      text: "button_pressed",
    };
  
    if (store_nativeSystemType_value == "android")
    {
      // Send the msg object as a JSON-formatted string.
      Android.setActivities(JSON.stringify(msg))
      //webSocket.send(JSON.stringify(msg));
    }
    
  } 
  
  export function startChallenge() {
    sendText();
  }
  
  

  
  async function sendVal(id,value) {
    // Construct a msg object containing the data the server needs to process the message from the chat client.
    var msg = {
      type: "action",
      text: id,
      value: value
    };
  
    if (store_nativeSystemType_value == "android")
    {
      // Send the msg object as a JSON-formatted string.
      Android.setActivities(JSON.stringify(msg))
    }
    
  }   