import * as yup from 'yup';

let values = {
    email: "",
    name: "",
    age: "",
    fibers: "",
    checkbox: false,
};

export const forumSchema = yup.object().shape({
  email: yup.string().required("Email is required")
    .email("Email is invalid"),
  name: yup.string().required("name is required"),
  age: yup.string().notRequired(),
  fibers: yup.string().required("selecting leakage type is required"),
  checkbox: yup.bool().required("Checkbox must be accepted")
    .oneOf([true], "Checkbox must be accepted"),
});

const validationResult = forumSchema
    .validate(values, { abortEarly: false })
    .then(() => {
      alert(JSON.stringify(values, null, 2));
    })
    .catch((err) => {
      console.log(err.errors);
    });