<script lang="ts">
  import { onMount } from "svelte";
  import LevelCircle from "./LevelCircle.svelte";
  import { timeFormat,TrainingLevel,TrainingLevels } from "../utils/utils";
  import { Player, Video, DefaultUi } from '@vime/svelte'

  export var currentTrainingLevel:Number = 1;


  $: time = TrainingLevels[currentTrainingLevel].duration * TrainingLevels[currentTrainingLevel].repeats * 2;

  $: timeTxt = timeFormat(time);

  $: trainingLevel = currentTrainingLevel;
  var maxTrainingLevel = 5;

 
  var myVideo = document.getElementById("video1"); 

function playPause() { 
  if (myVideo.paused) 
    myVideo.play(); 
  else 
    myVideo.pause(); 
} 

function increaseLevel() {
 
   currentTrainingLevel += 1;
   if (currentTrainingLevel>4)
     currentTrainingLevel = 0;
  }

</script>

