<script lang="ts">
  import { createEventDispatcher } from "svelte";
  import { onMount } from "svelte";
  import {
    store_macAddress,
    store_BLEconnected,
    store_currentEventFromSensor,  /* save this in localstorage ! */
    store_currentEventFromBLEreading,
  } from "../stores";
  import { BTLE_Action, BTLE_Struct } from "../utils/main-view";
  import { storeVallocal, convertToLeak, getleaks} from "../utils/dataStorage";

  const dispatch = createEventDispatcher();

  export let src = "";

  // local variables
  var expectingFirmwareVersion: boolean = false;
  var eventStr: string = "[";
  var readingOutEvent: boolean = false;
  var eventNR = 0;

  onMount(() => {
    (<any>document.getElementById("BTLE")).addEventListener(
      "data-event",
      BTLEdatachanged
    );
    setInterval(_handleGetSensor, 10000);
    eventStr = "[";

   // var a = getleaks();
    
  });

  function _handleGetSensor() {
    // update this.connectedStr when the BTLE's stage changes
    if ((<any>document.getElementById("BTLE")).connectedStr === "connected") {
      readSensorEvents();
    } else this.firstread = true;
  }

  function uintToString(array: any) {
    var view = new Uint8Array(array.buffer);
    var result = "";
    for (var i = 0; i < array.byteLength - 1; i++) {
      // -1 because last is string terminate  (0)
      result += String.fromCharCode(view[i]);
    }
    return result;
  }

  function validateEvent(input: string) {
    var retVal: boolean = false;

    // search for "TMR" in eventStr
    var pos = input.indexOf("CUR_EVT"); // or if something else is the latest check for that
    if (pos > -1) {
      retVal = true;
    }
    return retVal;
  }

  function reached_end_of_event_readout(input: string) {
    var return_value = false;
    if (readingOutEvent && validateEvent(input)) {
      readingOutEvent = false;
      return_value = true;
    } else {
      return_value = false;
    }

    return return_value;
  }

  function processInput(input: string) {
    // check if input contains a valid JSON string
    try {
     
      // check if json_string is terminated with a "}"
      if (input.charAt(input.length - 1) != "}") {
        input = input + "}"; // TMR value is sometimes to long and is missing the } so add it
      }
      var json = JSON.parse(input);
    } catch (e) {
      console.log("Error parsing JSON: " + e);
      // chop off the last } we just added
      var input = input.substring(0, input.length - 1);
      if (expectingFirmwareVersion) {
        expectingFirmwareVersion = false; // its ok to this only here because normal json communication is bypassing this if statement
        //   String str2 = chopOffBadCharacters(input);
        /* convert input hexadecimal text into a integer */
        var number: number = 0;
        // for (int i = 0; i < str2.length() && ((str2.charAt(i) != '}')); i++) {
        //     if ((str2.charAt(i) >= '0') && (str2.charAt(i) <= '9')) {
        //         number = (number * 16) + (str2.charAt(i) - '0');
        //     }
        //     if ((str2.charAt(i) >= 'A') && (str2.charAt(i) <= 'F')) {
        //          number = (number * 16) + (str2.charAt(i) - 'A' + 10); // A = 10 F = 16
        //      }
        //      if ((str2.charAt(i) >= 'a') && (str2.charAt(i) <= 'f')) {
        //          number = (number * 16) + (str2.charAt(i) - 'a' + 10); // A = 10 F = 16
        //      }
        //  }

        if (number > 0 && number < 20000) {
          // store the firmware version
          storeVallocal("firmwareVersion", number);
          console.log("Sensor Version: " + number);
        }
      }
      return; // nothing to do here anymore
    }

    {
      //

      if (reached_end_of_event_readout(input)) {
        // if we have reached the end of the event readout
        // we have reached the end of the event readout
        // now we can process the event
        // we have to convert the event into a JSON object
        try {
          eventStr = eventStr.substring(0, eventStr.length - 1); // hack for now .. remove the last ,
          eventStr += "]";
          var json = JSON.parse(eventStr);
        } catch (e) {
          eventStr = "";
          console.log("Error parsing JSON: " + e);
          return;
        }
        var eventIsComplete: Promise<boolean>;
        var pos = eventStr.indexOf("LEAK");
        if (pos > -1) {
          eventIsComplete = convertToLeak(json);
        }
        // and reset the event
        eventStr = "[";
     //   if (eventIsComplete) {
          $store_currentEventFromBLEreading += 1;
      //  }
      } else {
        // we have not reached the end of the event readout
        // so we need to add the data to the event
        eventStr += input;
        eventStr += ",";
      }
    }
  }

  function getEventNrFromSensorIfAvailable(input:string)
  {
    var eventNrSTr = "";
    var eventNr = 0;

    var pos = input.indexOf("CUR_EVT");
    if (pos > -1) {
      var json = JSON.parse(input);
      eventNrSTr = json["CUR_EVT"];
      // convert eventNr to number  
      eventNr = parseInt(eventNrSTr);

    }
    return eventNr;
  }
  
  async function BTLEdatachanged(evt: any) {
    var value = evt.detail.message;

    if (typeof value === "string" || value instanceof String) {
      if (value.includes("connected")) {
        $store_macAddress = value.substring(10);
        $store_BLEconnected = true;
      }
    } else {
      //
      var ml = stringFromArray(value);
      console.log(" adc Val : " + ml);
       // chop off the last character (string terminator)
       var ml = ml.substring(0, ml.length - 1);

      // string to integer
      var number: number = parseInt(ml);
      storeVallocal("firmwareVersion", number);
      var eventNr = getEventNrFromSensorIfAvailable(ml);
      if (eventNr > 0) {
        $store_currentEventFromSensor = eventNr;
      }

      processInput(ml);
    }
  }
  //dispatch("child-click-video", {code}) // send an event with the code to the caller

  function stringFromArray(data: DataView) {
    var count = data.byteLength;
    var str = "";
    var val = 0;

    for (var index = 0; index < count; index += 1) {
      val = data.getUint8(index);
      str += String.fromCharCode(val);
    }
    return str;
  }

  function stringToArray(txt: string) {
    var count = txt.length;
    //var str = "";
    var val = 0;
    var buffer = new ArrayBuffer(count);
    var view = new Int8Array(buffer);

    for (var index = 0; index < count; index += 1) {
      view[index] = txt.charCodeAt(index);
    }
    var data = new DataView(buffer);
    return data;
  }

  function readSensorEvents() {
    if (readingOutEvent == false) {
      if ($store_currentEventFromBLEreading < $store_currentEventFromSensor) {
        readingOutEvent = true;
        // eventNR += 1;
        console.log(
          "readout event : " + ($store_currentEventFromBLEreading + 1)
        );
        var str = "{'R_EVT':" + ($store_currentEventFromBLEreading + 1) + "}";
        let btled: BTLE_Struct[] = [
          { serviceNr: 0, action: BTLE_Action.write, data: stringToArray(str) },
          { serviceNr: 0, action: BTLE_Action.read, data: null },
        ];

        (<any>document.getElementById("BTLE")).btledata = btled;
      }
    } else {
      (<any>document.getElementById("BTLE")).read = true; // btle component uses mutex so it will wait for write to finish
    }
  }
</script>

<lsg-bluetooth id="BTLE" name="BLE" />

