<script>
    import { _ } from 'svelte-i18n'
    import '../css/w3.css';
    import '../css/app.css';
    import {store_db, store_weeknr} from '../stores.js';
    import {store_nativeSystemType} from '../stores.js'
    

    function saveToDB()
    {
     // var content = "test"
     // async (content) => {
     //   await store_db.put('settings', content, 'content');
     // }
      // weeknr is pass on from bridje.js into the store
  
     // if ($nativeSystemType == 'ios') // hack for now..
     // {
          if (window.localStorage.getItem('weeknr') == null)
          {
            window.localStorage.setItem('weeknr',1)
            store_weeknr.set(1)
          }
          else
          {
             var nr = window.localStorage.getItem("weeknr")
             nr = parseInt(nr) + 1
             window.localStorage.setItem("weeknr", nr)
             store_weeknr.set(nr)
          }
      //}
      
      const txtJumpsThisWeek = "Jumpsweek" + $store_weeknr
      const txtSecondsThisWeek = "Secondsweek" + $store_weeknr

      let valJumps = document.getElementById("nrJumps").value;
      let valSeconds = document.getElementById("contractionTime").value;
      
      console.log(txtJumpsThisWeek)
      console.log(txtSecondsThisWeek)
      console.log(valJumps)
      console.log(valSeconds)

      window.localStorage.setItem(txtJumpsThisWeek, valJumps);
      window.localStorage.setItem(txtSecondsThisWeek, valSeconds);
 
      window.location.href='#/ActivitiesScreen';
    }
</script>


<head>
    <!-- w3.ccs  is from the w3css website ! 
    <link rel="stylesheet" href="/css/w3.css">  
    <link rel="stylesheet" href="/css/app.css"> -->
</head>


<h2 id="HowDitItGoTxt" style = "margin-top: 3vh"  class = "center extraWide txtBig"> {$_('howDidItGoScreen.How did it go')} ? </h2>


<!-- Show the image. -->
<div class="img1 center images"  style = "background-image: url('./CarinChallenge_Howdiditgo.png')"></div>
 
<h2 id="JumpTxt" class = "center extraWide txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;">  {$_('howDidItGoScreen.How many jumps could you do until you had a leakage')} ? </h2>

<!-- Show the number of jumps input. -->
<div class="takeupSpaceForNumberInput"></div>

<div class="centeredInput">
  <div class="number-input ">
    <button onclick="this.parentNode.querySelector('input[type=number]').stepDown()" ></button>
    <input id="nrJumps" class="quantity" min="0" name="quantity" value="1" type="number">
    <button onclick="this.parentNode.querySelector('input[type=number]').stepUp()" class="plus"></button>
  </div>
</div>

<div class="takeupSpaceForNumberInput"></div>
<!-- End Show the number of jumps input. -->


<!-- Show the image. -->
<div class="img2 center images" style=" background-image: url('./CarinChallenge_Howdiditgo002.png');"></div>

<h2 id="HowLongTxt" class = "center extraWide txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;"> {$_('howDidItGoScreen.How many seconds could you hold the contraction during the squat')} ? </h2>

<!-- Show the number of jumps input. -->
<div class="takeupSpaceForNumberInput"></div>

<div class="centeredInput">
  <div class="number-input ">
    <button onclick="this.parentNode.querySelector('input[type=number]').stepDown()" ></button>
    <input id="contractionTime" class="quantity" min="0" name="quantity" value="1" type="number">
    <button onclick="this.parentNode.querySelector('input[type=number]').stepUp()" class="plus"></button>
  </div>
</div>

<div class="takeupSpaceForNumberInput"></div>
<!-- End Show the number of jumps input. -->

<button class="center"  style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  font-size:large " on:click={saveToDB} >
  {$_('Continue')}
</button>
 