<script>
    import { _ } from 'svelte-i18n';
    import { locale, locales } from 'svelte-i18n'
    import {bothFibers, fastFibers, slowFibers, endChallenge} from '../utils/bridge.js'
   // import { nativeSystemType } from '../stores.js';

  var options = ['ltr','rtl']
  var option = 'ltr'
 
  $: { document.dir = option }

  // subscribe to the nativeType store to check if we use android or ios
  
 
 /*  this part is for the selection handling of the activity selectableItem class is only used for this not for styling*/ 
 var divItems = document.getElementsByClassName("selectableItem");
 
 var blockActivitySelection = false;   // if checkbox is vinked block the selection of the activities. Set here outside the selected() function so it is global  
                                       //  and will be remembered the next time we call the function 
 var leakage_sneezing = false;
 var leakage_exercising = false;
 var leakage_sleeping = false;
 var leakage_standing = false;
    
 function goBack() {
   if (blockActivitySelection == false)
   {
     var fast = false;
     var slow = false;
     if ((leakage_standing == true) || (leakage_sleeping == true))
     {
       slow = true;
     }
     if ((leakage_sneezing== true) || (leakage_exercising == true))
     {
       fast = true;
     }
     if ((slow==true) && (fast==true))
     {
        bothFibers()
     }
     else
     {
       if (slow)
         slowFibers()
       if (fast)
         fastFibers()
     }
   }
   
   
   setTimeout(function() {
     //your code to be executed after x milisecond
     window.location.href='#/EndScreen';
     //endChallenge()  // delay the closing of the window so fast/slow fibers command has some time to process
   }, 500);
   
 }
   
  
 
 
 function selected(item) {
     item = document.getElementById(item);
     if (item.id == "noLeakage")  // this function is called from the checkbox (no leakage)
     {
       clear();               // erase all previous selections (reset colors)
       if (item.checked == true){
         blockActivitySelection = true;
       } else {
         blockActivitySelection = false;
       }
 
     }
     else
     {
       if (blockActivitySelection == false)  // when blocked do not change color or change settings
       {
          var highlight_on = true;   // should the item be highlighted
          switch(item.id)
          {
             
             case "leakage_standing":
               leakage_standing = !leakage_standing  // toggle boolean selection 
               if (!leakage_standing)
                 highlight_on = false;
               break;
             case "leakage_sleeping":
               leakage_sleeping = !leakage_sleeping  // toggle boolean selection 
               if (!leakage_sleeping)
                 highlight_on = false;
               break;
             case "leakage_exercising":
               leakage_exercising = !leakage_exercising  // toggle boolean selection 
               if (!leakage_exercising)
                 highlight_on = false;
               break;
             
             case "leakage_sneezing":
             leakage_sneezing = !leakage_sneezing  // toggle boolean selection 
               if (!leakage_sneezing)
                 highlight_on = false;
               break;
          } 
          if (highlight_on == true)
             item.style.backgroundColor = '#dbd5db';
           else
             item.style.backgroundColor = '#f2f2f2'
          
        //   slowFibers()
         
       }
     }
     document.getElementById("ContinueTxt").style.backgroundColor = '#15B4B0'; 	
 }
 
 function clear() {
     for(var i=0; i < divItems.length; i++) {
         var item = divItems[i];
         item.style.backgroundColor = '#f2f2f2';
     }
     leakage_sneezing = false;   // also reset the activities
     leakage_exercising = false;
     leakage_sleeping = false;
      leakage_standing = false;
 }
 
  
</script>


<style>
 
 
  /* some defines tobe used in different classes so we always have the same values */
  :root {
    /* here we use vh as unit wich is perc of the screen height so it scales with the screen size height */
   --custom-height_number_input: 9vh; /* the size of the number input*/
 }
 
 
  /* activity selection images */ 
  img{
     
    display: block;
    height: 15vh;
    width: 15vh;
 
  }
  
 
 /*  when scrolling is not needed anymore  enable next line 
 html, body {margin: 0; height: 100%; overflow: hidden} */
  
 /* center is used to center the text and other block elements */
 .center {
   display: block;
   margin-left: auto;
   margin-right: auto;
   margin-top: 5%;
   width: 50%;
 }
 
 .extraWide {
 
   width: 30%;
 }
 
 .txt{
 
   text-align: center;
   font-size: 2vh;
   color:
 #7e6779;
   
 }
 
 
 
 /* settings for the continue button */
 button {
     margin: auto;
   margin-top: 5vh;
   background-color: #7e6779; 
   border: none;
   color: white;
   text-align: center;
   text-decoration: none;
   display: block;
   font-size: 4vh;
   width : 50vw;
   height: 10vh;
   border-radius: 9px;
 }
 
 /* to display the activity images next to eachother we use a flexbox            */
 /* see: https://www.quackit.com/css/flexbox/tutorial/  for more details how to  */ 
 /* use a flexbox                                                                */
 
 .row {
   display: flex;
   justify-content: center;
   border: none;
 
 }
 
 .columnImage {
   flex-grow: 0; 
   flex-shrink: 0; 
   flex-basis:25%;
   border: none;
   justify-content: center;
 
 }
 
 body {
     background-color: #f2f2f2;
 }
 
 
 </style>
 <body>
 
  
 
 <!-- Show the activity images. -->
 <h2 id="LeaksDuringTxt" class = "center extraWide txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; margin-top: 15vh;" >   {$_('activityScreen.You have a leakage during')}?  </h2>
 <div id="activityFeedback" class="row"> <!-- class row is a flexbox -->
   <div id="leakage_exercising" class="columnImage selectableItem" on:click={() => selected("leakage_exercising")} style="cursor: pointer;">
   
     <img src="./leakage_exercising.svg" alt="fast" style="width:100%" > 
   <figcaption id="HighActivityTxt" class="caption center txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;"> {$_('activityScreen.Exercising')} </figcaption>
   </div>
   <div id="leakage_sleeping" class="columnImage selectableItem" on:click={() => selected("leakage_sleeping")}  style="cursor: pointer;">
     <img src="./leakage_sleeping.svg" alt="slow" style="width:100%">
   <figcaption id="LowActivityTxt" class="caption center txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;"> {$_('activityScreen.Sleeping')}</figcaption>
  
 </div>

  
 </div>
 
 
 <div id="activityFeedback" class="row"> <!-- class row is a flexbox -->
   <div id="leakage_standing" class="columnImage selectableItem" on:click={() => selected( "leakage_standing" )} style="cursor: pointer;">
   
     <img src="./leakage_standstil.svg" alt="slow" style="width:100%">
   <figcaption id="LowActivityTxt" class="caption center txt"style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;"> {$_('activityScreen.Standing still')}</figcaption>
   </div>
   <div id="leakage_sneezing" class="columnImage selectableItem"  on:click={() => selected("leakage_sneezing")} style="cursor: pointer;">
     <img src="./leakage_sneeze.svg" alt="fast" style="width:100%">
   <figcaption id="HighActivityTxt" class="caption center txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;">{$_('activityScreen.Sneezing')} </figcaption > 
   </div>
   
 </div>
 <div id="checkbox" class="checkbox " style="display: flex;align-items: center;margin-top: 6vh;justify-content: center;align-content: stretch;flex-wrap:nowrap;flex-direction: row;" >
 <input type="checkbox" id="noLeakage" name="lastOption" value="noLeakage"  style="cursor: pointer;" on:click={() => selected("noLeakage")}>
 <label for="noLeakage" class = "txt" style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; color:#7e6779;
 " > {$_('activityScreen.I have no leakage')} </label>
 
 </div>
 
 <button  id="ContinueTxt" type="button" on:click={goBack} style="font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; font-size:large ">   {$_('Continue')} </button>
 
 </body>

