</script>

<svg
  id="test"
  class="borderRed"
  viewBox="0 0 100 100"
  style="height:100%; width:100%"
>
  <g id="moveBox">
    <path use:cssVariables={{ strokeWidth }} id="foo" d={pathLine(data.D3signal)} />
    <path use:cssVariables={{ strokeWidth }} id="pathForMarker" style="stroke:white"  />
    <circle id="circle" r={radius} cx="0" cy="0" fill="white" class="circle" />
  </g>
</svg>

<style>
  path {
    stroke: rgb(51, 51, 54);
    stroke-width: var(--strokeWidth);
    fill: none;
    stroke-linecap: round;
  }

  .borderRed {
     border: 1px solid red; 
  }

  .axis {
    font: 4px sans-serif;
  }

  svg {
    max-height: 100%;
    max-width: 100%;
  }

  .transformToMiddle {
    position: relative;
    left: 0%;
    transform: translate(0%, 20%);
