import { store_fibers } from './stores.js';
import { dataStructures_weekNrVideosLookup, eFibers } from './utils/dataStructures';
import { jsonVideos } from "./videos/en_exercises_codes";
var eFiberIndex;
(function (eFiberIndex) {
    eFiberIndex[eFiberIndex["STANDARD"] = 0] = "STANDARD";
    eFiberIndex[eFiberIndex["SLOW"] = 1] = "SLOW";
    eFiberIndex[eFiberIndex["FAST"] = 2] = "FAST";
})(eFiberIndex || (eFiberIndex = {}));
// get list of videos for weeknr
export function getVideosForWeeks(weeknrs) {
    // since we are outside a svelte component use  TODO: pass to function ??
    let store_fibersValue;
    const unsubscribe = store_fibers.subscribe(value => {
        store_fibersValue = value;
    });
    var indexNrs = []; // return value
    var selectedVideosJson = [];
    // do not use for, in construction only works for strings and any ???
    for (var i = 0; i < weeknrs.length; i++) // go over the requested week numbers 
     {
        for (var fiberIndex = 0; fiberIndex < 3; fiberIndex++) // fiber index [old videos, slow, fast]
         {
            var shouldAdd = false;
            if (fiberIndex == eFiberIndex.STANDARD)
                shouldAdd = true;
            if ((fiberIndex == eFiberIndex.SLOW) && ((store_fibersValue == eFibers.BOTH) || (store_fibersValue == eFibers.SLOW)))
                shouldAdd = true;
            if ((fiberIndex == eFiberIndex.FAST) && ((store_fibersValue == eFibers.BOTH) || (store_fibersValue == eFibers.FAST)))
                shouldAdd = true;
            if (shouldAdd == true) {
                let weeknr = weeknrs[i];
                let index = dataStructures_weekNrVideosLookup[fiberIndex][weeknr - 1]; // weeknr is from 1,2,,,8  array is from 0,,7
                //console.log(index)
                // indexNrs.push(2)
                for (var x = 0; x < index.length; x++) {
                    if (!indexNrs.includes(index[x])) {
                        indexNrs.push(index[x]);
                        selectedVideosJson.push(jsonVideos[index[x]]);
                    }
                }
            }
        }
        // add a terminator code so the GUI can add a weeknumber row in the list ???
    }
    unsubscribe; // unsubscribe from svelte store 
    return selectedVideosJson;
}
// test functions 
export function testVideos() {
    var weeknrs = [1, 2];
    var result = [];
    result = getVideosForWeeks(weeknrs);
    console.log(result);
    // TODO check the result
}
