import * as Realm from "realm-web";
import 'uuid';
/*  ------------ defines -----------  */
var firstTime = true;
const app = new Realm.App({ id: "carinrealmapp-dpocn" });
async function login() {
    // Create an anonymous credential
    const credentials = Realm.Credentials.anonymous();
    try {
        // Authenticate the user
        const user = await app.logIn(credentials);
        // `App.currentUser` updates to match the logged in user
        //assert(user.id === app.currentUser.id)
        return user;
    }
    catch (err) {
        console.error("Failed to log in", err);
    }
}
export async function RealmStoreUserAndLog(logs, userInfo) {
    var result = null;
    var user = await login();
    // var userinfo:string = JSON.stringify(userInfo);
    // var log:string = JSON.stringify(logs);
    if (user) {
        result = await user.functions.AddData(logs, userInfo);
    }
    return result;
}
