<script>
  import {
    select as d3_select,
    selectAll as d3_selectAll,
    easeLinear as d3_easeLinear,
  } from "d3";
  import { onMount } from "svelte";

  export var trainingLevel = 1;
  export let maxTrainingLevel = 5;

  var r = 50;

  $: {
    dataIsChanged(trainingLevel+1); // $: means it is reactive in svelte, so when data change the function is called
  }

  function dataIsChanged(trainingLevel) {
    var max_level = maxTrainingLevel;

    d3_selectAll("#circleLevel > *").remove();
    var colorlevel = (100 / maxTrainingLevel) * trainingLevel;
    var colorlevelStr = colorlevel.toString() + "%";

    console.log(colorlevelStr);
    var svg = d3_select("#circleLevel");

    var grad = svg
      .append("defs")
      .append("linearGradient")
      .attr("id", "grad")
      .attr("x1", "0%")
      .attr("x2", "0%")
      .attr("y1", "100%")
      .attr("y2", "0%");
    grad
      .append("stop")
      .attr("offset", "0%")
      .attr("stop-opacity", "1")
      .style("stop-color", "#31B8B6");
    grad
      .append("stop")
      .attr("offset", colorlevelStr)
      .attr("stop-opacity", "1")
      .style("stop-color", "#31B8B6");
    grad
      .append("stop")
      .attr("offset", colorlevelStr)
      .attr("stop-opacity", "1")
      .style("stop-color", "#E2E2E2");
    grad
      .append("stop")
      .attr("offset", "100%")
      .attr("stop-opacity", "1")
      .style("stop-color", "#E2E2E2");

    svg
      .append("circle")
      .attr("cx", r)
      .attr("cy", r)
      .attr("r", r)
      .attr("fill", "url(#grad)");
  }

  onMount(() => dataIsChanged(trainingLevel+1));
</script>

<svg
  viewBox="0 0 100 100"
  id="circleLevel"
  style=" width:100%; height:100%; "
/>
