

<script>

    export let xAxisName = 'Weeks';
	  export let yAxisName = 'Jumps';
    export let weeks = [2,4,6,8,10,12];

    import { onMount } from 'svelte';

      function buildup()
      {
        var a = "M "
        var points_yvals = ['421.248', '431.735', '443.683', '455.631','467.579', '479.527', '491.475', '503.423', '515.371','527.319','539.267', '551.215', '563.163', '575.111', '587.059','599.007', '610.955', '622.903', '634.851', '646.799', '658.747','671.857']
        var points_xvals = [ '614.409', '643.699', '672.604', '701.541', '730.283', '759.192', '788.097','816.952']
        var dots = ["dot1Seconds","dot2Seconds","dot3Seconds","dot4Seconds","dot5Seconds","dot6Seconds","dot7Seconds","dot8Seconds"]
        
        var wk = 0;
        var value = 0;
        
        //var weeks = [2,4,10,3,10,11,18,20]; // the results per week
        var weeknr = 0;
        
        for (const week in weeks) {
          
          let yval = points_yvals[weeks[weeknr]]
          let xval = points_xvals[weeknr]
          if (weeknr > 0)
          {
            a = a + " L " + xval + "," + yval
          }else
          {
            a = a + xval + "," + yval
          }
          document.getElementById(dots[weeknr]).setAttribute('cx',xval)
          document.getElementById(dots[weeknr]).setAttribute('cy',yval)
          weeknr = weeknr + 1
        }
        if (weeknr > 0)
        {
            document.getElementById(dots[weeknr-1]).setAttribute('stroke','#15b3af')
            document.getElementById(dots[weeknr-1]).setAttribute('fill',"#15b3af")
        }
        if (weeknr < 8)
        {
           while (weeknr < 8)  // remove all other week dots
           {
              document.getElementById(dots[weeknr]).setAttribute('visibility','hidden')
              weeknr = weeknr + 1
           }
        }
        
        //a = "M 224.862,460.905 L 253.698,480.671 L 282.572,480.671 L 311.422,520.203 L 340.296,520.203 L 369.218,579.501 L 398.016,638.799 L 426.859,658.569"
        //a = "M 585.504,443.683 L 614.409,443.683 L 643.699,443.683 L 672.604,468.079 L 701.541,491.912 L 730.283,503.923 L 759.192,575.611 L 788.097,623.403 L 816.952,659.065"
        document.getElementById('lineSeconds').setAttribute('d',a)
        console.log(a)
        //document.getElementById('Layer 1').appendChild(
      }
      
      onMount( 
         () => buildup()
       );


       
        
