<style>
	*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.wrapper {
  display: grid;
  
  text-align: center;
  border-style: solid;
  border-color: red;
  /* use min/max value so it never grows larger */
  grid-template-columns: minmax(0, 1fr) minmax(0,16fr) minmax(0, 1fr);
  /* 2px is for the 2px wide borther */
  grid-template-rows: minmax(0,1fr) 2px minmax(0,10fr) ;
  

  grid-gap: 1px;
  width: 100vw;
  height: 100vh;
}

.row-border{
    border-top: 2px solid gray;
    grid-column: 1 / 4; /* this code makes the row stretch to entire width of the container */
}

 img{
     
	 display: block;
	 height: 100%;
	 width: 100%;
  
   } 

/* too make text centered in grim-items, https://stackoverflow.com/questions/45536537/centering-in-css-grid */
.grid-item-centered-contents {
  position: relative;
  text-align: center;
}

span {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
/* too make text centered in grim-items end */


</style>


