    export let xAxisName = 'Weeks';
	  export let yAxisName = 'Jumps';
    export let weeks = [2,4,6,8,10,12];


    import { onMount } from 'svelte';

      function buildup()
      {
        var a = "M "
        var points_yvals = ['421.516', '441.139', '460.905', '480.671','500.437', '520.203', '520.203', '539.969', '579.501','599.267','619.033', '638.799', '658.569']
        var points_xvals = ['224.862', '253.698', '282.572', '311.422', '340.296', '369.218', '398.016', '426.859']
        var dots = ["dot1","dot2","dot3","dot4","dot5","dot6","dot7","dot8"]
        
        var wk = 0;
        var value = 0;
        
      //  var weeks = [2,4,6,8,10,12]; // the results per week
        var weeknr = 0;
        
        for (const week in weeks) {
          
          let yval = points_yvals[weeks[weeknr]]
          let xval = points_xvals[weeknr]
          if (weeknr > 0)
          {
            a = a + " L " + xval + "," + yval
          }else
          {
            a = a + xval + "," + yval
          }
          document.getElementById(dots[weeknr]).setAttribute('cx',xval)
          document.getElementById(dots[weeknr]).setAttribute('cy',yval)
          weeknr = weeknr + 1
        }
        if (weeknr > 0)
        {
            document.getElementById(dots[weeknr-1]).setAttribute('stroke','#15b3af')
            document.getElementById(dots[weeknr-1]).setAttribute('fill',"#15b3af")
        }
        if (weeknr < 8)
        {
           while (weeknr < 8)  // remove all other week dots
           {
              document.getElementById(dots[weeknr]).setAttribute('visibility','hidden')
              weeknr = weeknr + 1
           }
        }
        
        //a = "M 224.862,460.905 L 253.698,480.671 L 282.572,480.671 L 311.422,520.203 L 340.296,520.203 L 369.218,579.501 L 398.016,638.799 L 426.859,658.569"
        
        document.getElementById('line').setAttribute('d',a)
        console.log(a)
        //document.getElementById('Layer 1').appendChild(
      }
      
      onMount( 
         () => buildup()
       );


       
        
