export var eContractRelax;
(function (eContractRelax) {
    eContractRelax[eContractRelax["CONTRACT"] = 0] = "CONTRACT";
    eContractRelax[eContractRelax["RELAX"] = 1] = "RELAX";
    eContractRelax[eContractRelax["CURVE_UP"] = 2] = "CURVE_UP";
    eContractRelax[eContractRelax["CURVE_DOWN"] = 3] = "CURVE_DOWN";
    eContractRelax[eContractRelax["START"] = 4] = "START";
})(eContractRelax || (eContractRelax = {}));
let DURATION = 5;
export let TICKS_PER_SEC = 10;
export let nrOfTicksPerTypes = { contract: DURATION * TICKS_PER_SEC,
    relax: 3 * TICKS_PER_SEC,
    curve: 1 * TICKS_PER_SEC,
    start: 4 * TICKS_PER_SEC
};
var UtilsTick = 0;
export var eTrainingStatus;
(function (eTrainingStatus) {
    eTrainingStatus[eTrainingStatus["IDLE"] = 0] = "IDLE";
    eTrainingStatus[eTrainingStatus["PLAY"] = 1] = "PLAY";
    eTrainingStatus[eTrainingStatus["PAUSED"] = 2] = "PAUSED";
    eTrainingStatus[eTrainingStatus["STOP"] = 3] = "STOP";
})(eTrainingStatus || (eTrainingStatus = {}));
export var TrainingLevels = [
    { number: 1,
        duration: 5,
        repeats: 5,
        text: "Beginner"
    },
    { number: 2,
        duration: 6,
        repeats: 10,
        text: "Basic"
    },
    { number: 3,
        duration: 8,
        repeats: 15,
        text: "Intermediate"
    },
    { number: 4,
        duration: 10,
        repeats: 20,
        text: "Advanced"
    },
    { number: 5,
        duration: 12,
        repeats: 25,
        text: "Expert"
    }
];
export function timeFormat(nrSeconds) {
    var sec_num = nrSeconds;
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);
    if (hours < 10) {
        hours = "0" + hours;
    }
    if (minutes < 10) {
        minutes = "" + minutes;
    }
    if (seconds < 10) {
        seconds = "0" + seconds;
    }
    return minutes + ':' + seconds;
}
export function addTicks(action, data, nrTicks) {
    if (action == eContractRelax.START)
        action = eContractRelax.RELAX; // same signal/path as for relax
    for (var x = UtilsTick; x < (UtilsTick + nrTicks); x++) {
        var ElementEntry = { tick: x, ContractOrRelax: action };
        data.push(ElementEntry);
    }
    UtilsTick += nrTicks;
    return data;
}
function createPart(action, data) {
    /* add the action to the timeline */
    data.typeTimeline.push({ tick: UtilsTick, ContractOrRelax: action });
    switch (action) {
        case eContractRelax.START:
            addTicks(action, data.D3signal, nrOfTicksPerTypes.start);
            break;
        case eContractRelax.CONTRACT:
            addTicks(action, data.D3signal, nrOfTicksPerTypes.contract);
            break;
        case eContractRelax.RELAX:
            addTicks(action, data.D3signal, nrOfTicksPerTypes.relax);
            break;
        case eContractRelax.CURVE_UP:
            addTicks(eContractRelax.RELAX, data.D3signal, nrOfTicksPerTypes.curve / 2); //  half signal down
            addTicks(eContractRelax.CONTRACT, data.D3signal, nrOfTicksPerTypes.curve / 2); //  half signal down
            break;
        case eContractRelax.CURVE_DOWN:
            addTicks(eContractRelax.CONTRACT, data.D3signal, nrOfTicksPerTypes.curve / 2); //  half signal down
            addTicks(eContractRelax.RELAX, data.D3signal, nrOfTicksPerTypes.curve / 2); //  half signal down
            break;
    }
}
// not used anymore, delete ?
export function createSignal(duration, nrOfRepeats) {
    var data = { D3signal: [], typeTimeline: [] };
    var time = 0;
    var currentAction = eContractRelax.RELAX;
    UtilsTick = 0;
    createPart(eContractRelax.START, data);
    for (var x = 0; x < nrOfRepeats * 2; x++) {
        // flip contract/release
        if (currentAction == eContractRelax.RELAX) {
            createPart(eContractRelax.CURVE_UP, data);
            createPart(eContractRelax.CONTRACT, data);
            currentAction = eContractRelax.CONTRACT;
        }
        else {
            createPart(eContractRelax.CURVE_DOWN, data);
            createPart(eContractRelax.RELAX, data);
            currentAction = eContractRelax.RELAX;
        }
    }
    return data;
}
